

.container {
    display: flex;
    margin: 6rem 0 5rem;
}


.alternatives-div {
    display: grid;
    grid-template-columns: 30vw 30vw;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    margin-bottom: 3rem;
}

.alternatives-div button {
    height: 150px;
    width: 600px;
    background: linear-gradient(to right, rgb(127, 247, 168), rgb(74, 222, 128), rgb(34, 197, 94));
    color: whitesmoke;
    font-size: 3rem;
    border-radius: 3rem;
    cursor: pointer;
}

.alternatives-div button:hover {
    background: linear-gradient(to right, rgb(34, 197, 94), rgb(21, 128, 61));
}

.scroll-btn-div {
    display: inline-flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100vw;
    margin-bottom: 2rem;
}

.scroll-btn-div button {
    width: 300px;
    height: 80px;
    background: conic-gradient(at right top, rgb(199, 210, 254), rgb(71, 85, 105), rgb(199, 210, 254));
    border-radius: 3rem;
    font-size: 2rem;
}

.scroll-btn-div button:hover {
    background: conic-gradient(at right top, rgb(139, 150, 139), rgb(54, 68, 85), rgb(139, 150, 139));
    color: whitesmoke;
}

