
h1 {
    text-align: center;
    margin: 10rem 0 4rem;
    font-size: 4rem;
    text-decoration: underline;
}

.categories {
    display: grid;
    grid-template-columns: 15vw 15vw 15vw 15vw;
    justify-content: center;
    row-gap: 3vw;
    column-gap: 5vw;
    margin: 2rem 0;
}

.category-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20%;
    width: 300px;
    height: 200px;
    background: linear-gradient(to right, rgb(134, 239, 172), rgb(192, 132, 252));
    border: 1px solid black;
    border-radius: 2rem;
    text-decoration: none;
    color: #000;
    cursor: pointer;
}

.category-card:hover {
    background: linear-gradient(to right, rgb(251, 113, 133), rgb(217, 70, 239), rgb(99, 102, 241));
}

img {
    width: 75px;
    height: 75px;
}

.input-div {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 2rem;
    font-size: 1.75rem;
}

.input-div input {
    width: 70px;
    height: 50px;
    margin-top: 0.5rem;
    font-size: 1.75rem;
    text-align: center;
    background: transparent;
    border-radius: 1rem;
}