

nav {
    display: inline-flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-top: 1rem;
}

nav a {
    display: flex;
    margin-left: 2rem;
    align-items: center;
}

nav div {
    display: inline-flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;
}

nav div a img {
    width: 150px;
}